<template>
    <div class="md-example-child md-example-child-tabs md-example-child-tab-bar-4">
        <md-tab-bar
                v-model="current"
                :items="items"
                :has-ink="false"
                @change="onTabChange"
        >
            <template slot="item" slot-scope="{ item }">
                <div :ref="item.ref" :id="item.ref" class="custom-item">
                    <div class="img-cover">
                        <img :src="item.src" alt="">
                    </div>
                    <div class="text">
                        <span v-text="item.label"></span>
                    </div>
                </div>
            </template>
        </md-tab-bar>
    </div>
</template>

<script>
import { TabBar, Icon } from 'mand-mobile'

export default {
  name: 'tab-bar',
  /* DELETE */
  title: '自定义内容',
  titleEnUS: 'Custom Item',
  /* DELETE */
  components: {
    [TabBar.name]: TabBar,
    [Icon.name]: Icon
  },
  mounted () {
    this.initTabBarStatus()
  },
  watch: {
    $route (to) {
      console.log(to)
      this.items.forEach((item) => {
        if (to.path === '/') {
          this.current = 1
          this.items[0].src = require('@/assets/tabBar/icon_' + this.items[0].ref + '_pressed@2x.png')
        }
        if (item.link === to.path) {
          this.current = item.name
          item.src = require('@/assets/tabBar/icon_' + to.name + '_pressed@2x.png')
        } else {
          item.src = require('@/assets/tabBar/icon_' + item.ref + '_default@2x.png')
        }
      })
    }
  },
  data () {
    return {
      current: 1,
      items: [
        { name: 1, label: '首页', ref: 'home', link: '/home', src: require('@/assets/tabBar/icon_home_default@2x.png') },
        { name: 2, label: '资讯', ref: 'news', link: '/news', src: require('@/assets/tabBar/icon_news_default@2x.png') },
        { name: 3, label: '科技库', ref: 'scienceBank', link: '/scienceBank', src: require('@/assets/tabBar/icon_scienceBank_default@2x.png') },
        { name: 4, label: '我的', ref: 'personal', link: '/personal', src: require('@/assets/tabBar/icon_personal_default@2x.png') }
      ]
    }
  },
  methods: {
    // 切换标签
    onTabChange (val) {
      this.items.forEach((item) => {
        if (item.ref === val.ref) {
          item.src = require('@/assets/tabBar/icon_' + val.ref + '_pressed@2x.png')
        } else {
          item.src = require('@/assets/tabBar/icon_' + item.ref + '_default@2x.png')
        }
      })
      this.$router.push(val.link)
    },
    initTabBarStatus () {
      console.log(this.$route)
      this.items.forEach((item, key) => {
        if (item.ref === this.$route.name) {
          this.current = item.name
          item.src = require('@/assets/tabBar/icon_' + this.$route.name + '_pressed@2x.png')
        } else {
          item.src = require('@/assets/tabBar/icon_' + item.ref + '_default@2x.png')
        }
      })
    }
  }
}

</script>

<style lang="stylus">
    .md-example-child-tab-bar-4
        .custom-item
            display flex
            flex-direction column
            align-items center
            justify-content center
            height 100%
            flex 1
            .text
                font-size 20px
                margin-top 10px
                font-weight bold
            .img-cover
                display flex
                justify-content center
                align-items center
                width 40px
                height 40px
                img
                    width 30px
        .is-active
            img
                width 60px!important
</style>
