import store from '../store'
import axios from 'axios'
import { Toast, Dialog } from 'mand-mobile'
import { removeToken } from '@/utils/auth' // getToken from cookie
// 创建axios实例
const service = axios.create({
  // baseURL: process.env.BASE_API, // api 的 base_url
  timeout: 5000 // 请求超时时间
})
const whiteList = [
  'sendSmsVerificationCode',
  'loginByPhoneAndPwd'
]
// request拦截器
service.interceptors.request.use(
  config => {
    let data = config.data
    // 判断当前请求是否需要加token
    let needToken = true
    whiteList.forEach(item => {
      if (config.url.includes(item)) {
        needToken = false
      }
    })
    if (needToken) {
      config.headers['Authorization'] = store.getters.token
      config.data = data
    } else {
      config.data = data
    }
    // if (store.getters.token) {
    //   config.headers['Token'] = store.getters.token // 让每个请求携带自定义token 请根据实际情况自行修改
    //   /* config.headers['fapiao_token'] = getToken() // 让每个请求携带自定义token 请根据实际情况自行修改 */
    //   /* config.headers['fapiao_token'] = '1e00be39-9ddb-4673-90e6-1dd1fb8b36f0' // 让每个请求携带自定义token 请根据实际情况自行修改 */
    // }
    return config
  },
  error => {
    // Do something with request error
    console.log(error) // for debug
    Promise.reject(error)
  }
)
// response 拦截器
service.interceptors.response.use(
  response => {
    /**
     * code为非0000是抛错 可结合自己业务进行修改
     */
    const res = response.data
    if (!res.code) {
      return res
    }
    if (res.code === 1) {
      // 50008:非法的token; 50012:其他客户端登录了;  50014:Token 过期了;
      return Promise.resolve(res)
    } else {
      if (res.code === 1001) {
        Dialog.alert({
          title: '提示',
          content: '用户信息已过期，请重新登录',
          cancelText: '取消',
          confirmText: '确定',
          onConfirm: () => store.dispatch('Logout').then(() => {
            // location.reload() // 为了重新实例化vue-router对象 避免bug
          })
        })
        return Promise.reject(new Error(res.message || 'Error'))
      } else {
        return Promise.reject(res.message)
      }
    }
  },
  error => {
    console.log('err' + error) // for debug
    return Promise.reject(error)
  }
)

export default service
