import request from '../utils/request'
import apiPath from './url'
import qs from 'qs'

export function login (data) {
  return request({
    url: apiPath.login.login,
    method: 'get',
    params: data
  })
}
export function sendCode(data) {
  return request({
    url: apiPath.login.sendCode,
    method: 'get',
    params: data
  })
}
export function testCode(data) {
  return request({
    url: apiPath.login.testCode,
    method: 'post',
    params: data,
  })
}
export function verifyEmail(data) {
  return request({
    url: apiPath.login.verifyEmail,
    method: 'post',
    params: data,
  })
}
export function sendEmail(data) {
  return request({
    url: apiPath.login.sendEmail,
    method: 'get',
    params: data,
  })
}
export function register (data) {
  data = qs.stringify(data)
  console.log(data)
  return request({
    url: apiPath.login.register,
    method: 'post',
    data: data,
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded'
    }
  })
}
// 修改密码
export function resetPsw (data) {
  console.log(data)
  return request({
    url: apiPath.login.resetPsw,
    method: 'get',
    params: data
  })
}
// 获取数据字典
export function getDictionary (data) {
  return request({
    url: apiPath.login.dictionary,
    method: 'post',
    data: qs.stringify(data)
  })
}
