import router from './router'
import NProgress from 'nprogress' // progress bar
import 'nprogress/nprogress.css'// progress bar style
import store from './store'
import { getToken } from '@/utils/auth' // getToken from cookie
import { Toast, Dialog } from 'mand-mobile'

const whiteList = ['/', '/login', '/register', '/resetPsw', '/home',
  '/news', '/news/detail', '/scienceBank', '/activity/index', '/activity/detail', '/technology/index',
  '/technology/detail', '/scienceResult/index', '/scienceResult/detail', '/device/index',
  '/device/detail', '/result/index', '/case/index', '/case/detail', '/video/index', '/video/detail',
  '/policy/index', '/policy/detail', '/expert/index', '/expert/detail',
  '/unit/index', '/unit/detail', '/project/index', '/project/detail',
  '/search/index' ] // 不重定向白名单
router.beforeEach((to, from, next) => {
  NProgress.start() // start progress bar
  if (getToken()) { // determine if there has token
    /* has token */
    next()
  } else {
    if (whiteList.indexOf(to.path) > -1) {
      next()
    } else {
      Dialog.alert({
        title: '提示',
        content: '用户信息已过期，请重新登录',
        cancelText: '取消',
        confirmText: '确定',
        onConfirm: () => next(`/login?redirect=${to.path}`) // 否则全部重定向到登录页
      })
      NProgress.done()
    }
  }
})
router.afterEach(() => {
  NProgress.done() // 结束Progress
})
