import { getDictionary } from '@/apiUrl/login'

const system = {
  state: {
    dictionary: []
  },
  mutations: {
    SET_DICTIONARY: (state, data) => {
      state.dictionary = data
    }
  },
  actions: {
    // 登录
    getDictionary ({ commit }, formData) {
      return new Promise((resolve, reject) => {
        getDictionary(formData).then(res => {
          commit('SET_DICTIONARY', res.data)
          resolve()
        }).catch(error => {
          reject(error)
        })
      })
    }
  }
}

export default system
